// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import colors from 'vuetify/es5/util/colors'
import axios from 'axios'
import UtiltyJS from './js/UtilityFunction.js'
//import MyFilter from './js/FilterFunction.js'
import FilterFunction from './js/FilterFunction.js'

let url = "/iNursingSys/static/conf/setting.json"
if(window.location.href == "http://localhost:8085/"){
  //build之前位置
  url = "/static/conf/setting.json"
}

axios.get(url)
  .then(response =>{
    //console.log(response.data)
    startUp(response.data);
  })
  .catch(error =>  {
    console.log("get Setting json: error : " + error);
  });


//--- 個人 ---
//var baseURL = 'http://172.16.2.33:8080';
//var FormViewer_url ="http://172.16.2.33:8080/iFormSys/form-template/";
function startUp(settingContent){
  Vue.config.productionTip = false;

  Vue.use(Vuex);
//Vue.use(axios)
//個人 axios.defaults.baseURL = 'http://172.16.2.33:8080';
//公司
  axios.defaults.baseURL = settingContent.baseURL;
//醫院 axios.defaults.baseURL = 'http://192.168.0.228:8080';

//前端測試請使用 '/iNursingSrv' WebService
  Vue.prototype.$http = axios;
  Vue.prototype.$UtilityJs = UtiltyJS;


  const store = new Vuex.Store({
    state:{
      loginUser:{
        empNo:'',
        empName:'',
        idNo:'',
        passwd:'',
        userRole:'',
        userDept:'',
        loginDateTime:'',
        keyId:''
      },
      patientInfo:{
        chartNo:'',
        ptName:'',
        bedNo:'',
        sex:'',
        serno:'',
        admitDays:'',
        ckinDate:''
      },
      SystemControl:{
        AppHome_patientListStatus:true,
        AppHome_webCamStatus:false,
        AppHome_snackbar:{
          status:false,
          text:'',
          timeout:4000,
          closeText:''
        },
        //公司
        FormViewer_url:settingContent.FormViewer_url

      },
    },
    mutations : {
      loginProc(state, user) {
        state.loginUser = user;
        state.loginUser.loginDateTime = new Date();
      },
      logoutProc(state){
        state.loginUser = '';
      },
      selectPt(state, patientObj){
        state.patientInfo = patientObj;
      },
      clearPt(state) {
        state.patientInfo = '';
      },
      togglePtList(state){
        state.SystemControl.AppHome_patientListStatus = !state.SystemControl.AppHome_patientListStatus;
      },
      toggleWebCam(state){
        state.SystemControl.AppHome_webCamStatus = !state.SystemControl.AppHome_webCamStatus;
      },
      setSnackBar(state,obj){
        state.SystemControl.AppHome_snackbar = obj;
      }
    }
  });

  Vue.use(Vuetify, {
    theme: {
      primary: colors.deepPurple.lighten2,
      secondary: colors.deepPurple.lighten4,
      accent:colors.deepPurple.accent1,
      error:colors.red.darken3
    }
  });

  /* eslint-disable no-new */
  new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>'
  });

}







